import React, { PureComponent } from "react";
import PropTypes from "prop-types";
import styles from "../style.css";

export default class Pokemon extends PureComponent {
    static propTypes = {
        name: PropTypes.string.isRequired,
        url: PropTypes.string.isRequired,
        onPokemonClick: PropTypes.func
    };

    constructor(props) {
        super(props);

        this.state = {
            abilities: []
        };
    };

    clickHandler = () => {
        const { onPokemonClick } = this.props;
        const { url } = this.props;
        if (typeof onPokemonClick === "function") {
            onPokemonClick(url);
        }
    };

    render() {
        const { name, url } = this.props;
        const { abilities } = this.state;
        const id = parseInt(url.slice(34, -1))
        let imgPath = "pokemons-images/"+id+".png";
        return (
            <pokemon onClick={this.clickHandler}>
                <ul>
                    <li>Название: {name}</li>
                    <li>Номер: {id}</li>
                    <li>Способности: {abilities.join(", ")}</li>
                </ul>
                <img src={imgPath} />
            </pokemon>
        );
    };
}